import React, { useState } from 'react'

const submitRecharge = () => {
    const checkBox = document.querySelectorAll('#recharge input')
    let sum = 0
    checkBox.forEach(elem => {if (elem.checked) sum+=Number(elem.value)})
    location.href = `http://localhost:3000/recharge/process?money=${sum/10000}`
}

const Recharge = () => {
    const [authorizeToken] = useState(Math.floor(Math.random() * 999999 + 100000))
    var [disableRecharge, setDisable] = useState(true)

    const checkAuthorizeToken = () => {
        if (event.key==='Enter') return document.getElementById('submit-recharge').click()
        if (Number(event.target.value) === authorizeToken) return setDisable(false)
        setDisable(true)
    }

    return (
        <div id='recharge' className='single-announcement'>
            <ul>
                <li>
                    <label>
                        <input type='checkbox' name='money' value='10000'></input>
                    10000
                    </label>
                </li>
                <li>
                    <label>
                        <input type='checkbox' name='money' value='20000'></input>
                    20000
                    </label>
                </li>
                <li>
                    <label>
                        <input type='checkbox' name='money' value='50000'></input>
                    50000
                    </label>
                </li>
                <li>
                    <label>
                        <input type='checkbox' name='money' value='100000'></input>
                    100000
                    </label>
                </li>
                <li>
                    <label>
                        <input type='checkbox' name='money' value='200000'></input>
                    200000
                    </label>
                </li>
                <li>
                    <label>
                        <input type='checkbox' name='money' value='500000'></input>
                    500000
                    </label>
                </li>
                <span>CODE: {authorizeToken}</span><br/>
                <input type='text' onKeyUp={checkAuthorizeToken}></input>
                <button id='submit-recharge' disabled={disableRecharge} onClick={submitRecharge}>RECHARGE</button>
            </ul>
        </div>
    )
}

export default Recharge