import React from 'react'
import ReactDOM from 'react-dom'
import { BrowserRouter, Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'
import '@babel/polyfill'
import './style/style.scss'

import Frame from './components/default/Frame'
import Home from './components/Home/Home'
import Login from './components/Authorize/Login'
import Register from './components/Authorize/Register'
import NotAuthorize from './components/Authorize/NotAuthorize'
import NotFound from './components/NotFound'
import User from './components/User'
import Cart from './components/Cart/Cart'
import Payment from './components/Cart/Payment'
import Recharge from './components/Recharge/Recharge'
import RechargeResult from './components/Recharge/RechargeResult'
import Item from './components/Item'
import Stock from './components/Stock/Stock'

import store from './store/store'

const root = document.getElementById("root")

ReactDOM.render(
    <Provider store={store}>
        <BrowserRouter>
            <Switch>
                <Route path='/' exact={true}><Frame Component={Home} /></Route>
                <Route path='/login' exact={true}><Frame Component={Login} /></Route>
                <Route path='/register' exact={true}><Frame Component={Register} /></Route>
                <Route path='/notauthorize' exact={true}><Frame Component={NotAuthorize} /></Route>
                <Route path='/user' exact={true}><Frame Component={User} /></Route>
                <Route path='/cart' exact={true}><Frame Component={Cart} /></Route>
                <Route path='/stock' exact={true}><Frame Component={Stock} /></Route>
                <Route path='/item/:name' exact={true}><Frame Component={Item} /></Route>
                <Route path='/payment' exact={true}><Frame Component={Payment} /></Route>
                <Route path='/recharge' exact={true}><Frame Component={Recharge} /></Route>
                <Route path='/recharge/result' exact={true}><Frame Component={RechargeResult} /></Route>
                <Route><Frame Component={NotFound} /></Route>
            </Switch>
        </BrowserRouter>
    </Provider>
, root)