import React from 'react'
import cookie from 'cookie'

const checkLoginStatus = () => {
    event.preventDefault()
    const $statusContainer = document.getElementById('status')
    const username = document.querySelector("input[name='username']").value
    const password = document.querySelector("input[name='password']").value
    $statusContainer.textContent = 'Loading...'
    fetch(`/login/process?username=${username}&password=${password}`, {
        method: 'POST'
    }).then(response => {
        if (!response.ok) return $statusContainer.textContent = 'Network error'
        return response.json()
    }).then(data => {
        if (data.error !== false) return $statusContainer.textContent = data.error
        location.pathname = '/'
    }).catch(e => {
        $statusContainer.textContent = e.message
    })
}

const Login = () => {
    const {username, token} = cookie.parse(document.cookie)
    if (username && token && username.length && token.length) location.pathname = '/'
    return (
        <div id='login'>
            <form onSubmit={checkLoginStatus}>
                <table>
                    <tbody>
                        <tr>
                            <td></td>
                            <td className='auth-header'>LOGIN</td>
                        </tr>
                        <tr>
                            <td>Username</td>
                            <td>
                                <input type='text' name='username' required /><br />
                            </td>
                        </tr>
                        <tr>
                            <td>Password</td>
                            <td>
                                <input type='password' name='password' required />
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <p id='status'></p>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <input type='submit' value='Log in' />
                            </td>
                        </tr>
                    </tbody>
                </table>
            </form>
        </div>
    )
}

export default Login