import React from 'react'

const checkRegisterStatus = () => {
    event.preventDefault()
    const username = document.querySelector("input[name='username']").value
    const password = document.querySelector("input[name='password']").value
    const email = document.querySelector("input[name='email']").value
    const fullname = document.querySelector("input[name='fullname']").value
    const gender = document.querySelector("select[name='gender']").value
    const birthday = document.querySelector("input[name='birthday']").value
    const address = document.querySelector("input[name='address']").value

    const $statusContainer = document.getElementById('status')
    $statusContainer.textContent = 'Loading...'
    fetch(`/register/process?username=${username}&password=${password}&email=${email}&fullname=${fullname}&gender=${gender}&birthday=${birthday}&address=${address}`, {
        method: 'POST'
    }).then(response => {
        if (!response.ok) return $statusContainer.textContent = 'Network error'
        return response.json()
    }).then(data => {
        if (data.error !== false) return $statusContainer.textContent = data.error
        fetch(`/login/process?username=${username}&password=${password}`, {
            method: 'POST'
        }).then(response => {
            if (response.ok) location.pathname = '/'
            else location.pathname = '/login'
        })
    })
}

const Register = () => {
    return (
        <div id='register'>
            <form onSubmit={checkRegisterStatus}>
                <table>
                    <tbody>
                        <tr>
                            <td></td>
                            <td className='auth-header'>REGISTER</td>
                        </tr>
                        <tr>
                            <td>Username</td>
                            <td>
                                <input type='text' name='username' required minLength='4' maxLength='20' /><br />
                            </td>
                        </tr>
                        <tr>
                            <td>Password</td>
                            <td>
                                <input type='password' name='password' required minLength='4' maxLength='30' /><br />
                            </td>
                        </tr>
                        <tr>
                            <td>Email</td>
                            <td>
                                <input type='email' name='email' required /><br />
                            </td>
                        </tr>
                        <tr>
                            <td>Fullname</td>
                            <td>
                                <input type='text' name='fullname' required /><br />
                            </td>
                        </tr>
                        <tr>
                            <td>Gender</td>
                            <td>
                                <select name='gender' required>
                                    <option value={true}>Male</option>
                                    <option value={false}>Female</option>
                                </select><br />
                            </td>
                        </tr>
                        <tr>
                            <td>Birthday</td>
                            <td>
                                <input type='date' name='birthday' required /><br />
                            </td>
                        </tr>
                        <tr>
                            <td>Address</td>
                            <td>
                                <input type='text' name='address' required />
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <p id='status'></p>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>
                                <input type='submit' value='Register' />
                            </td>
                        </tr>
                    </tbody>
                </table>
            </form>
        </div>
    )
}

export default Register