import React from 'react'

const factionImgURLContainer = [
    {name: 'Royal_Navy', url:'https://azurlane.koumakan.jp/w/images/8/86/Royalnavy_orig.png'},
    {name: 'Eagle_Union', url:'https://azurlane.koumakan.jp/w/images/2/21/Eagleunion_orig.png'},
    {name: 'Sakura_Empire', url:'https://azurlane.koumakan.jp/w/images/9/93/Sakuraempire_orig.png'},
    {name: 'Iron_Blood', url:'https://azurlane.koumakan.jp/w/images/f/f5/Ironblood_edited.png'}
]

const searchFaction = (input) => {
    localStorage.setItem('faction-selected', input)
    location.pathname = '/stock'
}

const Faction = () => {
    return (
        <div id='faction'>
            {
                factionImgURLContainer.map(faction => <a key={faction.name} onClick={() => {searchFaction(faction.name)}}><img src={faction.url}/></a>)
            }
        </div>
    )
}

export default Faction