const home = (state = {popularItems: [], randomItems: []}, action) => {
    switch(action.type) {
        case 'updatePopularItems': {
            return {
                ...state,
                popularItems: action.data,
            }
        }
        case 'updateRandomItems': {
            return {
                ...state,
                randomItems: action.data,
            }
        }
        default: return state
    }
}

export default home