const page = (state = {page: 1, maxPage: 0}, action) => {
    switch(action.type) {
        case 'go': {
            return {
                ...state,
                page: action.page
            }
        }

        case 'prev': {
            return {
                ...state,
                page: state.page > 1 ? state.page - 1 : state.page
            }
        }

        case 'next': {
            return {
                ...state,
                page: state.page < state.maxPage ? state.page + 1 : state.page
            }
        }

        case 'setMaxPage': {
            return {
                ...state,
                maxPage: action.maxPage
            }
        }
        default: return state
    }
}

export default page