import React from 'react'

const Payment = () => {
    const result = location.search.split('=')[1]
    return (
        <div id='payment' className='single-announcement'>
            {
                result === 'true' ?
                    <>
                        <h1>Thank you for buying</h1>
                        <a href='/stock'>Buy more?</a>
                    </> :
                    <>
                        <h1>Seem like you don't have enough money</h1>
                        <a href='/recharge'>Click here to recharge</a>
                    </>
            }
        </div>
    )
}

export default Payment