import React, { useEffect, useState } from 'react'
import { prevPage, nextPage, goPage } from '../../store/actionCenter'
import { connect } from 'react-redux'

const Navigation = ({ page, maxPage }) => {
    //trash = amount of page when maxPage <= 5
    var [trash, setTrash] = useState([])

    useEffect(() => {
        let __trash = ['']
        for (var i = 1; i < maxPage; i++) __trash.push('')
        setTrash(__trash)
    }, [maxPage])

    useEffect(() => {
        const $a = document.querySelectorAll('#navigation-page a')
        $a.forEach((elem) => {
            if (elem.textContent==page) elem.classList.add('active')
            else elem.classList.remove('active')
        })
    }, [page])

    return (
        <div id="navigation-page">
            <i className="fas fa-chevron-left" onClick={() => { prevPage() }}></i>
            {
                maxPage <= 5 ?
                    <>
                        {
                            trash.map((value, index) => <a key={index} onClick={() => { goPage(index + 1) }} className={index+1===1?'active':''}>{index + 1}</a>)
                        }
                    </> :
                    page <= 3 ?
                        <>
                            <a onClick={() => { goPage(1) }} className='active'>1</a>
                            <a onClick={() => { goPage(2) }}>2</a>
                            <a onClick={() => { goPage(3) }}>3</a>
                            <span>...</span>
                            <a onClick={() => { goPage(maxPage) }}>{maxPage}</a>
                        </> :
                        <>
                            <a onClick={() => { goPage(1) }} className='active'>1</a>
                            <span>...</span>
                            <>
                                {
                                    page === maxPage ?
                                        <a onClick={() => { goPage(maxPage) }}>{maxPage}</a> :
                                        <>
                                            <a onClick={() => { goPage(page) }}>{page}</a>
                                            <span>...</span>
                                            <a onClick={() => { goPage(maxPage) }}>{maxPage}</a>
                                        </>
                                }
                            </>

                        </>
            }
            <i className="fas fa-chevron-right" onClick={() => { nextPage() }}></i>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        page: state.page.page,
        maxPage: state.page.maxPage
    }
}

export default connect(mapStateToProps)(Navigation)