import store from './store'

const {dispatch} = store

const updateStock = async (page, faction, order, sortBy, sale, search) => {
    await fetch(`/stock/process?page=${page}&faction=${faction}&order=${order}&sortBy=${sortBy}&sale=${sale}&search=${search}`).then(response => response.json().then(data => {
        if (!data.error) {
            const dataLength = data.splice(0,1)
            dispatch({type:'updateStock', data})
            dispatch({type:'setMaxPage', maxPage: Math.ceil(dataLength / 9)})    
        }
    }))
}

const prevPage = () => {dispatch({type: 'prev'})}
const nextPage = () => {dispatch({type: 'next'})}
const goPage = (page) => {dispatch({type: 'go', page})}

const updateFilter = (faction, order, sortBy, sale) => {
    dispatch({type:'go', page: 1})
    dispatch({type:'updateFilter', faction, order, sortBy, sale})
}

const updateSearch = (search) => {
    dispatch({type:'updateSearch', search})
}
const clearSearch = () => {
    dispatch({type:'clearSearch'})
}

const updateRandomItems = (data) => {
    dispatch({type:'updateRandomItems', data})
}
const updatePopularItems = (data) => {
    dispatch({type:'updatePopularItems', data})
}

export {
    updateStock,
    updateFilter,
    updateSearch,
    clearSearch,
    prevPage,
    nextPage,
    goPage,
    updateRandomItems,
    updatePopularItems
}