import React, { useEffect, useState } from 'react'

const removeItem = (name, cart) => sessionStorage.setItem('cart', JSON.stringify(cart.filter((item) => item.name !== name)))

const numberOfItemEdit = (name, calc, cart) => {
    cart.forEach(item => {
        if (item.name === name) {
            switch (calc) {
                case '-': {
                    --item.amount
                    break
                }
                case '+': {
                    ++item.amount
                    break
                }
            }
        }
    });
    return sessionStorage.setItem('cart', JSON.stringify(cart))
}

const getSummary = (cart) => {
    let sum = 0
    cart.length === 0 ? sum= 0 :cart.forEach(item => {sum+= item.price * item.amount})
    return sum
}

const Cart = () => {
    var [cart, setCart] = useState(sessionStorage.getItem('cart') ? JSON.parse(sessionStorage.getItem('cart')) : [])
    var [address, setAddress] = useState(false)
    var [addressChecker, setAddressChecker] = useState(true)
    var [onClickDetect, triggerOnClickDetect] = useState(true)

    useEffect(() => {
        if (!sessionStorage.getItem('cart')) sessionStorage.setItem('cart', JSON.stringify([]))
        fetch('/user/address').then(response => response.json().then(data => {
            setAddress(data.address)
        }))
    }, [])
    
    useEffect(() => {
        setCart(JSON.parse(sessionStorage.getItem('cart')))
    }, [onClickDetect])

    const buyCart = () => {
        location.href = `http://localhost:3000/cart/buy?items=${JSON.stringify(cart)}`
        sessionStorage.setItem('cart', JSON.stringify([]))
    }

    return (
        <div id='cart' className='single-announcement'>
            <div id='your-cart'>
                <table>
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Amount</th>
                            <th>Price</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            cart === null ? <tr></tr> : 
                            cart.map(item =>
                                <tr key={Math.random() * 10}>
                                    <td>{item.name.replaceAll('_', ' ')}</td>
                                    <td>
                                        <i className="fas fa-minus" onClick={() => { numberOfItemEdit(item.name, '-', cart); triggerOnClickDetect(!onClickDetect) }} style={{marginRight: '5px'}} disabled={item.amount===0}></i>
                                        {item.amount}
                                        <i className="fas fa-plus" onClick={() => { numberOfItemEdit(item.name, '+', cart); triggerOnClickDetect(!onClickDetect) }} style={{marginLeft: '5px'}} disabled={item.amount===item.max}></i>
                                    </td>
                                    <td>{item.price}</td>
                                    <td onClick={() => { removeItem(item.name, cart); triggerOnClickDetect(!onClickDetect) }}>(X)</td>
                                </tr>
                            )
                        }
                    </tbody>
                    <tfoot>
                        <tr>
                            <th colSpan='2'>Summary</th>
                            <th>{getSummary(cart)}</th>
                            <th>
                                <button disabled={ addressChecker || cart.length === 0 ? true : false} onClick={buyCart}>SUBMIT</button>
                            </th>
                        </tr>
                    </tfoot>
                </table>
                <div id='your-address'>
                    <p>Is this your address?</p><input type='checkbox' onChange={() => {setAddressChecker(!addressChecker)}}/>
                    <p><b>{address}</b></p>
                </div>
            </div>
        </div>
    )
}

export default Cart