import React, { useEffect, useState } from 'react'

const getNameFromURL = (url) => {
    let name = []
    for (var i = url.length - 1; ; i--) {
        if (url[i] === '/') return name.reverse().join('')
        name.push(url[i])
    }
}

const Item = () => {
    const [item, setItem] = useState([])
    useEffect(() => {
        (async () => {
            const name = getNameFromURL(location.pathname)
            await fetch(`/item/process?name=${name}`).then(response => response.json().then(data => {
                if (data.error) return setItem(data.error)
                setItem(data)
            }))
        })()
    })

    return (
        <div id='item'>
            {
                item === [] ? <></> :
                    <>
                        <img src={item.url} />
                        <table>
                            <thead>
                                <tr>
                                    <th style={{width: '20%'}}></th>
                                    <th style={{width: '80%'}}></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td className="unname">Name</td>
                                    <td>{item.name}</td>
                                </tr>
                                <tr>
                                    <td className="unname">Faction</td>
                                    <td>{item.faction}</td>
                                </tr>
                                <tr>
                                    <td className="unname">Amount</td>
                                    <td>{item.amount}</td>
                                </tr>
                                <tr>
                                    <td className="unname">Price</td>
                                    <td>{item.price}</td>
                                </tr>
                            </tbody>
                        </table>
                    </>
            }
        </div>
    )
}

export default Item