import React, { useEffect } from 'react'
import { updatePopularItems, updateRandomItems } from '../../store/actionCenter'
import PopularItems from './PopularItems'
import RandomItems from './RandomItems'
import Faction from './Faction'

const Home = () => {
    useEffect(() => {
        (async () => {
            //random
            await fetch('/stock/home?type=random').then(response => response.json().then(data => {
                updateRandomItems(data)
            }))
            //popular
            await fetch('/stock/home?type=popular').then(response => response.json().then(data => {
                updatePopularItems(data)
            }))
        })()
    }, [])

    return (
        <div id='home'>
            <h1>Popular</h1>
            <PopularItems />
            <h1>Random</h1>
            <RandomItems/>
            <h1>Faction</h1>
            <Faction />
        </div>
    )
}

export default Home