import React, { useEffect, useState } from 'react'
import cookie from 'cookie'
import standardName from './utils/standardName'

const checkUserLogin = () => {
    const cookies = cookie.parse(document.cookie || '')
    if (cookies.token) return true
    return false
}

const Card = ({ name, amount, price, url }) => {
    if (!sessionStorage.getItem('cart') || sessionStorage.getItem('cart').length === 0) sessionStorage.setItem('cart', JSON.stringify([]))

    let [__amount, setAmount] = useState(amount)

    var disableAddBtn = true
    if (checkUserLogin()) disableAddBtn = false

    useEffect(() => {
        const cart = JSON.parse(sessionStorage.getItem('cart'))
        if (cart.length === 0) return
        cart.forEach(item => {
            if (item.name === name) return setAmount(__amount - item.amount)
        })
    }, [])

    const addItem = (max) => {
        setAmount(__amount - 1)
        const cart = JSON.parse(sessionStorage.getItem('cart'))
        let checkExistingItem = false
        cart.forEach(item => {
            if (item.name === name) {
                ++item.amount
                checkExistingItem = true
                return
            }
        });
        if (!checkExistingItem) cart.push({ name, price, amount: 1, max })
        sessionStorage.setItem('cart', JSON.stringify(cart))
    }

    return (
        <table className='card'>
            <tbody>
                <tr>
                    <td>
                        <img src={url} alt='img' />
                    </td>
                </tr>
                <tr>
                    <td>
                        <a href={`/item/${name}`} target='_blank'>
                            <p id='name'>{standardName(name)}</p>
                        </a>
                    </td>
                </tr>
                <tr>
                    <td>
                        <p>Amount: {__amount}</p>
                    </td>
                </tr>
                <tr>
                    <td>
                        <p>{price}<i className="fas fa-money-bill-wave-alt"></i></p>
                    </td>
                </tr>
                <tr>
                    <td>
                        <button disabled={disableAddBtn || !__amount} onClick={() => { addItem(amount) }}>ADD</button>
                    </td>
                </tr>
            </tbody>
        </table>
    )
}

export default Card