import React, { useEffect } from 'react'
import Card from './Card'
import { connect } from 'react-redux'

import { updateStock } from '../../store/actionCenter'

const Screen = ({ items, page, filter }) => {
    var {faction, order, sortBy, sale, search} = filter
    useEffect(() => {
        localStorage.removeItem('search-bar')
        localStorage.removeItem('faction-selected')
    }, [])

    useEffect(() => {
        (async () => {
            await updateStock(page, faction, order, sortBy, sale, search)
        })()
    }, [filter, page])

    return (
        <div id='screen'>
            {
                items.length === 0 ? <div id='screen-not-found'>Not found</div> :
                    items.map((item) => <Card key={item._id} name={item.name} amount={item.amount} price={item.price} url={item.url} />)
            }
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        items: state.stock.items,
        page: state.page.page,
        filter: state.filter
    }
}

export default connect(mapStateToProps)(Screen)