const filter = (state = {faction: localStorage.getItem('faction-selected') || '', order: '', sortBy: '', sale: false, search: localStorage.getItem('search-bar') || ''}, action) => {
    const {faction, order, sortBy, sale, search} = action

    switch(action.type) {
        case 'updateFilter': {
            return {
                ...state,
                faction,
                order,
                sortBy,
                sale
            }
        }
        case 'updateSearch': {
            return {
                ...state,
                search
            }
        }
        case 'clearSearch': {
            return {
                ...state,
                search: ''
            }
        }
        default: return state
    }
}

export default filter