import React from 'react'
import Auth from './Auth'
import Footer from './Footer'

import { updateSearch } from '../../store/actionCenter'

const convertSearchAble = (input) => {
    return input.replaceAll(' ', '_')
}

const detectSubmitSearch = (input) => {
    if (event.key === 'Enter' || input === 'mouse') {
        let $searchBar = document.querySelector('#search-bar input')
        let searchValue = convertSearchAble($searchBar.value)
        if (location.pathname !== '/stock') {
            localStorage.setItem('search-bar', searchValue)
            location.pathname = '/stock'
        }
        else {
            updateSearch(searchValue)
            $searchBar.value = ''
        }
    }
}

const Frame = ({ Component }) => {
    return (
        <>
            <div id='header'>
                <li>
                    <a href='/'><i className="fas fa-home fa-lg"></i>Home</a>
                </li>
                <li>
                    <a href='/stock'><i className="fas fa-box-open fa-lg"></i>Stocks</a>
                </li>
                <li id='search-bar'>
                    <input type='text' placeholder='search' onKeyDown={detectSubmitSearch} />
                    <i className="fas fa-search" onClick={() => {detectSubmitSearch('mouse')}}></i>
                </li>
                <Auth />
            </div>
            <div id='body'>
                <Component />
            </div>
            <div id='footer'>
                <Footer />
            </div>
        </>
    )
}

export default Frame