import React, { useState } from 'react'
import cookie from 'cookie'

const getUserName = (name) => {
    let split = name.split(' ')
    let _name = split[split.length-1]
    if (_name.length > 10) return _name.slice(0, 10, '...') + '...'
    return _name
}

const Auth = () => {
    const [display, setDisplay] = useState(false)
    const cookies = cookie.parse(document.cookie || '')

    const detectOutSiteClick = () => {
        if (event.target === document.querySelector('.fa-caret-down')) {
            setDisplay(!display)
            document.removeEventListener('click', detectOutSiteClick)
        }
        else {
            if (display) {
                setDisplay(false)
                document.removeEventListener('click', detectOutSiteClick)
            }
        }
    }

    document.addEventListener('click', detectOutSiteClick)

    return (
        <ul id='auth'>
            {
                cookies.token ?
                    <>
                        <li><a href='/cart'><i className="fas fa-shopping-cart fa-lg"></i>Your cart</a></li>
                        <li style={{ textTransform: 'none' }}>
                            <p><i className="fas fa-user fa-lg"></i>Hello, {getUserName(cookies.username)}</p>
                            <span id='user-functional-container' style={{ visibility: display ? 'visible' : 'hidden' }}>
                                <a href='/user'><i className="fas fa-address-card"></i>Information</a>
                                <a href='/recharge'><i className="fas fa-money-check-alt"></i>Recharge</a>
                                <a href='/logout'><i className="fas fa-sign-out-alt"></i>Logout</a>
                            </span>
                            <i className="fas fa-caret-down fa-lg"></i>
                        </li>
                    </> :
                    <>
                        <li><a href='/login'><i className="fas fa-sign-in-alt fa-lg"></i>Login</a></li>
                        <li><a href='/register'><i className="fas fa-user-plus fa-lg"></i>Register</a></li>
                    </>
            }
        </ul>
    )
}

export default Auth