import React from 'react'

const RechargeResult = () => {
    const result = location.search.split('=')[1]
    return (
        <div id='recharge-result' className='single-announcement'>
            {
                result === 'true' ? <h1>SUCCESS</h1> : 
                <span>
                    <h1>FAILED</h1>
                    <h3>Please check your connection</h3>
                </span>
            }
        </div>
    )
}

export default RechargeResult