import React, { useEffect, useState } from 'react'

const hidePassword = (pwd) => {
    const pwdLength = pwd.length
    let _s = ''
    for (var i = 0; i < pwdLength; i++) _s += '*'
    return _s
}

const hideEmail = (email) => {
    let _s = ''
    let emailNameIndex = email.search('@')
    let emailLength = email.length
    for (var i = 0; i < emailNameIndex; i++) {
        if (i < 3) _s += email[i]
        else _s += '*'
    }
    for (var i = emailNameIndex; i < emailLength; i++) _s += email[i]
    return _s
}

const User = () => {
    const [data, setData] = useState([])
    const [history, setHistory] = useState([])

    useEffect(() => {
        (async () => {
            await fetch('/user/process').then(response => response.json().then(data => {
                setData(data)
            }))
            await fetch('/history').then(response => response.json().then(data => {
                setHistory(data)
            }))
        })()
    }, [])

    return (
        <div id='user'>
            <div id='avatar'><i className="fas fa-user-tie"></i></div>
            <h1>Info</h1>
            <div id="info-container">
                <table>
                    <tbody>
                        <tr>
                            <td className="unname">Username</td>
                            <td>{data[0]}</td>
                        </tr>
                        <tr>
                            <td className="unname">Password</td>
                            <td>{data[1] ? hidePassword(data[1]) : ''}</td>
                        </tr>
                        <tr>
                            <td className="unname">Email</td>
                            <td>{data[2] ? hideEmail(data[2]) : ''}</td>
                        </tr>
                        <tr>
                            <td className="unname">Fullname</td>
                            <td>{data[3]}</td>
                        </tr>
                        <tr>
                            <td className="unname">Gender</td>
                            <td>{data[4] ? 'Male' : 'Female'}</td>
                        </tr>
                        <tr>
                            <td className="unname">Birthday</td>
                            <td>{data[5] ? data[5].substring(0, 10) : ''}</td>
                        </tr>
                        <tr>
                            <td className="unname">Address</td>
                            <td>{data[6]}</td>
                        </tr>
                        <tr>
                            <td className="unname">Money</td>
                            <td>{data[7]}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <h1>History</h1>
            <div id='history'>
                <table>
                    <thead>
                        <tr>
                            <th>STT</th>
                            <th>Name</th>
                            <th>Amount</th>
                            <th>Price</th>
                            <th>Total</th>
                            <th>Date</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            history.map((value, index) =>
                                <tr key={index + 1}>
                                    <td>{index + 1}</td>
                                    <td>{value.name.replaceAll('_', ' ')}</td>
                                    <td>{value.amount}</td>
                                    <td>{value.price}</td>
                                    <td>{value.price * value.amount}</td>
                                    <td>{value.date.slice(0,10)}</td>
                                </tr>
                            )
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default User