import React from 'react'
import { updateFilter, clearSearch } from '../../store/actionCenter'
import { connect } from 'react-redux'
import standardName from './utils/standardName'

const factions = ['', 'Bilibili', 'Dragon_Empery', 'Eagle_Union', 'Hololive', 'Iris_Libre', 'Iron_Blood', 'KizunaAI', 'Neptunia', 'Northern_Parliament', 'Royal_Navy', 'Sakura_Empire', 'Sardegna_Empire', 'Universal', 'Utawarerumono', 'Vichya_Dominion']

const doUpdateFilter = () => {
    const faction = document.getElementById('faction').value.replaceAll(' ', '_')
    const order = document.getElementById('order').value
    const sortBy = document.getElementById('sort-by').value
    const sale = document.getElementById('sale').checked
    updateFilter(faction, order, sortBy, sale)
}

const checkSelect = () => {
    const value = document.querySelector('#order').value
    const $sortBy = document.querySelector('#sort-by')
    value === '' ? $sortBy.disabled = true : $sortBy.disabled = false
}

const Filter = ({ search }) => {
    return (
        <div id='filter'>
            <h3><i className="fas fa-filter"></i>Filter</h3>
            <table id='filter-main'>
                <tbody>
                    <tr>
                        <td>
                            Key
                        </td>
                        <td style={{textTransform: 'none', overflowWrap: 'anywhere'}}>
                            :{search ? standardName(search) : '...'}
                            <span onClick={clearSearch}>{search ? <i className="fas fa-backspace" style={{marginLeft: '3px'}}></i> : ''}</span><br />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            Faction
                    </td>
                        <td>
                            <select id='faction'>
                                {factions.map(faction => <option key={faction}>{standardName(faction)}</option>)}
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            Order
                    </td>
                        <td>
                            <select id='order' onChange={checkSelect}>
                                <option></option>
                                <option>Popular</option>
                                <option>Price</option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            Sort by
                    </td>
                        <td>
                            <select id='sort-by' disabled>
                                <option></option>
                                <option>A-Z</option>
                                <option>Z-A</option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            Sale
                    </td>
                        <td>
                            <input type='checkbox' id='sale' />
                        </td>
                    </tr>
                    <tr>
                        <td colSpan='2'>
                            <button onClick={doUpdateFilter}>SUBMIT</button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        search: state.filter.search
    }
}

export default connect(mapStateToProps)(Filter)